
.evaluate {
  position: fixed;
  bottom: 0;
  z-index: 20;
  height: 0.96rem;
  width: 100%;
  max-width: 750px;
  background-color: #f23030;
  text-align: center;
  color: #fff;
  font-size: 0.36rem;
  line-height: 0.96rem;
}
.evaluate .eva-content {
    position: relative;
    display: inline-block;
    margin-left: 0.56rem;
}
.evaluate .eva-content:before {
      position: absolute;
      top: 50%;
      left: -0.56rem;
      content: '';
      width: 0.4rem;
      height: 0.4rem;
      margin-top: -0.2rem;
      display: inline-block;
      float: right;
      background: url("../../static/imgs/write.png") no-repeat;
      background-size: 100%;
      -webkit-background-size: 100%;
}



