
.header {
  border-bottom: 0.2rem solid #f0f2f5;
}
.header .person {
    position: relative;
}
.header .person:before {
      content: '';
      width: 7.5rem;
      z-index: -10;
      position: absolute;
      top: 0;
      display: block;
      height: 2.54rem;
      background: url(../assets/imgs/bg2.png) no-repeat 0 0;
      background-size: 100% auto;
      -webkit-background-size: 100% auto;
}
.header .person .head-set {
      display: none;
      position: relative;
      height: 0.3rem;
      margin-top: 0.52rem;
}
.header .person .head-set .back {
        position: absolute;
        left: 0.3rem;
        padding: 0.2rem;
        margin-left: 0.1rem;
        margin-top: -0.2rem;
}
.header .person .head-set .back:after {
          content: '';
          display: block;
          width: 0.2rem;
          height: 0.3rem;
          background: url("../assets/imgs/left-arrow.png") no-repeat;
          background-size: 100%;
          -webkit-background-size: 100%;
}
.header .person .head-set .share {
        position: absolute;
        right: 0.84rem;
        padding: 0.1rem;
        margin-left: 0.2rem;
        margin-top: -0.1rem;
}
.header .person .head-set .share:after {
          content: '';
          display: block;
          width: 0.3rem;
          height: 0.3rem;
          background: url("../assets/imgs/share.png") no-repeat;
          background-size: 100%;
          -webkit-background-size: 100%;
}
.header .person .head-set .set {
        position: absolute;
        top: 0.15rem;
        right: 0.3rem;
        padding: 0.2rem 0;
        margin-left: 0.3rem;
        margin-top: -0.2rem;
}
.header .person .head-set .set:after {
          content: '';
          display: block;
          width: 0.3rem;
          height: 0.05rem;
          background: url("../assets/imgs/set.png") no-repeat;
          background-size: 100%;
          -webkit-background-size: 100%;
}
.header .person .person-info {
      position: relative;
      top: 0.42rem;
      margin-bottom: 0.82rem;
}
.header .person .person-info .face {
        float: left;
        position: relative;
        margin-left: 0.6rem;
}
.header .person .person-info .face .pic {
          width: 2.56rem;
          height: 2.5rem;
          border-radius: 50%;
          overflow: hidden;
          box-shadow: 0 7px 14px #f5a8a3;
}
.header .person .person-info .face .pic img {
            width: 100%;
            height: 100%;
}
.header .person .person-info .face .name {
          position: absolute;
          left: 50%;
          bottom: -0.3rem;
          margin-left: -0.94rem;
          width: 1.88rem;
          height: 0.52rem;
          border-radius: 20px;
          background: linear-gradient(to right, #fe6109, #f31434);
          text-align: center;
          font-size: 0.34rem;
          line-height: 0.52rem;
          color: #fff;
          box-shadow: 0 5px 7px #f5a8a3;
}
.header .person .person-info .connect {
        margin-top: 1.35rem;
        margin-left: 0.26rem;
        float: left;
}
.header .person .person-info .connect .telephone {
          height: 0.5rem;
          line-height: 0.48rem;
          font-size: 0.28rem;
          color: #0076fb;
}
.header .person .person-info .connect .telephone #tele-call {
            display: block;
            float: right;
            margin-left: 0.1rem;
            width: 0.48rem;
            height: 0.48rem;
            background: url("../assets/imgs/call.png") no-repeat;
            background-size: 100%;
            -webkit-background-size: 100%;
}
.header .person .person-info .connect .number {
          font-size: 0.22rem;
          margin-top: 0.2rem;
}
.header .person .person-info .connect .address {
          margin-top: 0.25rem;
          font-size: 0.28rem;
}
.header .person .stars {
      width: 2.5rem;
      margin: 0.44rem auto 0.22rem;
}
.header .person .stars .item {
        width: 0.38rem;
        height: 0.38rem;
        margin-right: 0.05rem;
        display: inline-block;
}
.header .person .stars .light {
        background: url("../assets/imgs/main-light-star.png") no-repeat;
        background-size: 100%;
        -webkit-background-size: 100%;
}
.header .person .stars .dark {
        background: url("../assets/imgs/main-dark-star.png") no-repeat;
        background-size: 100%;
        -webkit-background-size: 100%;
}
.header .person .stars .half-star {
        background: url("../assets/imgs/half-star.png") no-repeat;
        background-size: 100%;
        -webkit-background-size: 100%;
}
.header .person .tags {
      text-align: center;
      height: 1.85rem;
      overflow: hidden;
      padding: 0 0.3rem 0.24rem;
      border-bottom: 1px solid #EAEAEA;
      display: -ms-flexbox;
      display: -webkit-box;
      display: box;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.header .person .tags .item {
        margin: 0.2rem 0.1rem 0 0;
        padding: 0.2rem 0.3rem;
        font-size: 0.26rem;
        color: #848689;
        background: #f8f8f8;
        border-radius: 15px;
}
.header .person .tags .item .left {
          margin-right: 0.16rem;
}
.header .person .tags2 {
      height: 1.79rem;
      overflow: hidden;
      box-sizing: border-box;
      padding: 0 0.3rem 0.14rem;
      border-bottom: 1px solid #EAEAEA;
}
.header .person .tags2 .item {
        float: left;
        width: 31.8%;
        text-align: center;
        box-sizing: border-box;
        margin: 0.2rem 0.14rem 0 0;
        padding: 0.2rem 0;
        font-size: 0.2rem;
        color: #848689;
        background: #f8f8f8;
        border-radius: 15px;
}
.header .person .tags2 .item .left {
          margin-right: 0.1rem;
}
.header .person .tags2 .item:nth-child(3n) {
          margin-right: 0;
}
.header .person .tags3 {
      height: 1.88rem;
      overflow: hidden;
      padding: 0 0.5rem 0.14rem;
      border-bottom: 1px solid #EAEAEA;
}
.header .person .tags3 .item {
        float: left;
        margin: 0.2rem 0.3rem 0 0;
        padding: 0.2rem 0.35rem;
        font-size: 0.26rem;
        color: #848689;
        background: #f8f8f8;
        border-radius: 15px;
}
.header .person .tags3 .item .left {
          margin-right: 0.16rem;
}
.header .person .tags-hide {
      height: auto;
}
.header .person .tags-show {
      height: 1.85rem;
}
.header .person #see-more {
      margin: 0.16rem;
      text-align: center;
      font-size: 0.24rem;
      color: #bfbfbf;
}
.header .person #see-more:after {
        content: '';
        display: inline-block;
        margin-left: 0.16rem;
        width: 0.32rem;
        height: 0.1rem;
}
.header .person .tag-up:after {
      background: url("../assets/imgs/down-arrow.png") no-repeat;
      background-size: 100%;
      -webkit-background-size: 100%;
}
.header .person .tag-down:after {
      background: url("../assets/imgs/up-arrow.png") no-repeat;
      background-size: 100%;
      -webkit-background-size: 100%;
}



