
.urge {
  position: absolute;
  display: block;
  bottom: -15%;
  left: 50%;
  margin: -1.03rem;
  width: 2.06rem;
  height: 0.62rem;
  text-align: center;
  line-height: 0.62rem;
  font-size: 0.3rem;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #f0f2f5;
}
.urge-disabled {
  background-color: rgba(0, 0, 0, 0.4);
}
.urge-pop {
  position: fixed;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 70%;
  text-align: center;
  height: 1.5rem;
  line-height: 1.5rem;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 16px;
  color: #fff;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



