
.send-info {
  border-bottom: 0.2rem solid #f0f2f5;
  padding: 0.3rem 0.5rem;
}
.send-info .up .earth {
    position: relative;
    float: left;
    width: 2.1rem;
    height: 1.7rem;
    background: url("../assets/imgs/earth.png") no-repeat;
    background-size: 100%;
    -webkit-background-size: 100%;
}
.send-info .up .earth .number {
      position: absolute;
      font-size: 10px;
      top: 14%;
      right: 25%;
      color: #f198a0;
}
.send-info .up .foot-print {
    float: right;
}
.send-info .up .foot-print .has-send {
      font-size: 0.38rem;
      color: #161418;
}
.send-info .up .foot-print .km {
      margin-top: 0.26rem;
      font-size: 0.28rem;
      color: #848689;
}
.send-info .up .foot-print .km .number {
        margin-right: 0.1rem;
        font-size: 0.46rem;
        color: #f23030;
}
.send-info .up .foot-print .circle {
      margin-top: 0.26rem;
      font-size: 0.28rem;
      color: #81838e;
}
.send-info .up .foot-print .circle .number {
        color: #f23030;
}
.send-info .down {
    margin-top: 0.38rem;
    text-align: center;
}
.send-info .down .item {
      float: left;
      width: 33.3%;
}
.send-info .down .item .title {
        font-size: 0.28rem;
        color: #81838e;
}
.send-info .down .item .content {
        margin-top: 0.28rem;
        font-size: 0.22rem;
        color: #81838e;
}
.send-info .down .item .content .number {
          font-size: 0.34rem;
          color: #161418;
}
.send-info .down .big-item {
      width: 50%;
}



