
#send-present {
  margin: 0.2rem;
}
#send-present .item {
    width: 1.6rem;
    text-align: center;
}
#send-present .item .pre-pic {
      display: table-cell;
      vertical-align: middle;
      width: 1.6rem;
      height: 1.6rem;
      overflow: hidden;
}
#send-present .item .pre-pic img {
        width: 100%;
        height: 100%;
}
#send-present .item .pre-name {
      width: 98%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 0.16rem;
      font-size: 0.3rem;
      color: #686868;
}
#send-present .item .pre-number {
      width: 98%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 0.16rem;
      font-size: 0.26rem;
      color: #ff7d7d;
}
#send-present .item .pre-btn {
      display: inline-block;
      margin-top: 0.16rem;
      margin-bottom: 0.08rem;
      border: 1px solid #f23030;
      border-radius: 15px;
      background: #fff6f6;
      padding: 0.1rem 0.25rem;
      font-size: 0.22rem;
      color: #f23030;
}



