
.map {
  position: relative;
  padding: 0.3rem;
  padding-bottom: 0;
  border-bottom: 0.2rem solid #f0f2f5;
}
.map .m-title {
    font-size: 0.3rem;
    margin-bottom: 0.3rem;
}
.map .map-pic {
    position: relative;
    width: 6.9rem;
    height: 5.18rem;
}
.map .map-pic .ten-map {
      width: 100%;
      height: 100%;
}
.map .map-pic .person {
      position: absolute;
      top: 0.3rem;
      left: 0.3rem;
}
.map .map-pic .person .run {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        display: block;
        width: 0.6rem;
        height: 0.7rem;
        background: url("../assets/imgs/people.png") no-repeat;
        background-size: 100%;
        -webkit-background-size: 100%;
}
.map .map-pic .person .distance {
        position: absolute;
        top: 0;
        left: 0.45rem;
        text-align: center;
        display: block;
        width: 4.2rem;
        height: 0.62rem;
        line-height: 2;
        font-size: 0.24rem;
        color: #fff;
        background: url("../assets/imgs/pop.png") no-repeat;
        background-size: 100%;
        -webkit-background-size: 100%;
}
.map .map-pic .destination {
      position: absolute;
      top: 2rem;
      left: 4rem;
      width: 0.54rem;
      height: 0.62rem;
      background: url("../assets/imgs/destination.png") no-repeat;
      background-size: 100%;
      -webkit-background-size: 100%;
}



