
.wrapper {
  margin: 0 auto;
  font-size: 0.16rem;
  color: #161418;
  margin-bottom: 0.96rem;
}
.wrapper .mint-popup {
    width: 100%;
}
.wrapper .mint-swipe {
    height: 3.23rem;
}
.wrapper .mint-swipe .mint-swipe-item img {
      width: 100%;
}
.wrapper .mint-swipe .mint-swipe-indicators .mint-swipe-indicator {
      background: #fff;
      opacity: 0.95;
}
.wrapper .mint-swipe .mint-swipe-indicators .is-active {
      width: 20px;
      border-radius: 5px;
}



